import { MatIV } from './minMatrix';
import { WebGL } from './webgl';
//TODO 型anyは良くない
function createMatrix() {
    var m = new MatIV();
    // 各種行列の生成と初期化
    var mMatrix = m.identity(m.create());
    var vMatrix = m.identity(m.create());
    var pMatrix = m.identity(m.create());
    var mvpMatrix = m.identity(m.create());
    // ビュー座標変換行列
    m.lookAt([0, 1, 3], [0, 0, 0], [0, 1, 0], vMatrix);
    // プロジェクション座標変換行列
    //TODO width, heightのハードコーディング
    var width = 300;
    var height = 300;
    m.perspective(90, width / height, 0.1, 100, pMatrix);
    // 各行列を掛け合わせ座標変換行列を完成させる
    m.multiply(pMatrix, vMatrix, mvpMatrix);
    m.multiply(mvpMatrix, mMatrix, mvpMatrix);
    return mvpMatrix;
}
function main() {
    var canvas = document.getElementById('canvas');
    var context = canvas.getContext('webgl');
    var webgl = new WebGL(context);
    webgl.gl.clearColor(0.0, 0.0, 0.0, 1.0);
    webgl.gl.clearDepth(1.0);
    webgl.gl.clear(webgl.gl.COLOR_BUFFER_BIT | webgl.gl.DEPTH_BUFFER_BIT);
    var vertexCode = "\n        attribute vec3 position;\n        uniform   mat4 mvpMatrix;\n\n        void main() {\n            gl_Position = mvpMatrix * vec4(position, 1.0);\n        }\n    ";
    var fragmentCode = "\n        void main(void){\n            gl_FragColor = vec4(1.0, 1.0, 1.0, 1.0);\n        }\n    ";
    var program = webgl.createProgram(vertexCode, fragmentCode);
    var vertices = [
        0.0, 1.0, 0.0,
        -1.0, -1.0, 0.0,
        1.0, -1.0, 0.0
    ];
    var vertexBuffer = [
        webgl.createVertexBuffer(vertices),
    ];
    //TODO なんかそうじゃない
    var attLocations = [
        webgl.gl.getAttribLocation(program, 'position'),
    ];
    var attStrides = [3];
    //wgldではcreateProgram()の時点で行っている
    webgl.gl.useProgram(program);
    webgl.setAttribute(vertexBuffer, attLocations, attStrides);
    var uniLocation = webgl.gl.getUniformLocation(program, 'mvpMatrix');
    var mvpMatrix = createMatrix();
    webgl.gl.uniformMatrix4fv(uniLocation, false, mvpMatrix);
    webgl.gl.clearColor(0.3, 0.3, 0.3, 1);
    webgl.gl.clear(webgl.gl.COLOR_BUFFER_BIT);
    webgl.gl.drawArrays(webgl.gl.TRIANGLES, 0, 3);
}
onload = function () {
    main();
};
